@use "abstracts/mixins/breakpoints" as breakpoint

.post-meta
  font-size: var(--font-size-sm)
  color: var(--color-fg-muted)

.post-meta-item
  display: inline-flex
  align-items: center
  gap: var(--space-xs)
  flex-wrap: wrap

.post-meta-primary
  display: inline-grid
  grid-auto-flow: column
  grid-auto-columns: max-content
  justify-content: start
  align-items: center
  gap: var(--fluid-space-sm) var(--fluid-space-md)

  @include breakpoint.max(lg)
    display: grid
    grid-auto-flow: initial
    grid-template-columns: repeat(2, 1fr)

    > .post-meta-item:nth-child(even)
      justify-self: end

  @include breakpoint.max(sm)
    grid-template-columns: 1fr

    > .post-meta-item
      justify-self: start

    > .post-meta-item:nth-child(even)
      justify-self: start

.post-meta-secondary
  display: flex
  align-items: start
  flex-direction: column
  flex-wrap: wrap
  row-gap: var(--space-sm)
  margin-block: var(--fluid-space-lg)
