@use "abstracts/modules/section" as *
@use "abstracts/modules/no-cursor" as *
@use "abstracts/modules/text-clip" as *
@use "abstracts/mixins/transition-properties" as *
@use "abstracts/mixins/breakpoints" as breakpoint

.post-content
  @extend %no-cursor

  h1,
  h2
    @extend %section

.posts
  display: flex
  flex-direction: column
  gap: var(--fluid-space-base)
  margin: var(--space-none)
  padding: var(--space-none)

.post-preview
  height: 15rem
  max-height: 100%

  @include breakpoint.max(lg)
    height: auto

  .post-series
    font-size: var(--font-size-sm)
    color: var(--color-fg-muted)
    margin-top: var(--fluid-space-sm)

.post-header
  display: flex
  flex-direction: column
  gap: var(--fluid-space-sm)

.post-title
  margin: var(--space-none)

.post-excerpt
  margin: var(--space-none)
  color: var(--color-fg-muted)

  @extend %text-clip

.post-footer
  margin-top: var(--fluid-space-lg)

  .post-footer-content
    display: flex
    justify-content: space-between
    align-items: center
    flex-wrap: wrap
    gap: var(--space-base)
    font-size: var(--font-size-sm)
    color: var(--color-fg-muted)

    .copyright
      flex: 1 1 auto

    .share-icons
      display: flex
      gap: var(--fluid-space-xs)
      align-items: center

      a
        display: inline-flex
        align-items: center
        justify-content: center
        padding: var(--fluid-space-xs)
        color: var(--color-fg-muted)

        @include transition-properties(color, $duration: var(--motion-duration-default), $easing: var(--motion-easing-smooth))

        &:hover
          color: var(--color-fg-default)

    @include breakpoint.max(md)
      flex-direction: column
      align-items: flex-start
      text-align: left
      gap: var(--space-md)
